import React, {useContext, useState} from 'react';
import {ModalContext} from '../context/ModalContext';

import Modal from '@material-ui/core/Modal';
import { makeStyles } from '@material-ui/core/styles';

function getModalStyle() {
    const top = 80;
    const left = 50;
  
    return {
      top: `${top}%`,
      margin: 'auto'
    };
}

const useStyles = makeStyles(theme => ({
    paper: {
      position: 'absolute',
      width: 300,
      scroll: 'auto',
      backgroundColor: theme.palette.background.paper,
      boxShadow: theme.shadows[5],
      padding: theme.spacing(2, 4, 3),

    },
}));

const Recetas = ({receta}) => {

    //configuracion del material ui
    const {modalStyle}= useState(getModalStyle);
    const [open,setOpen]= useState(false)

    const classes= useStyles();

    const handleOpen= () =>{
        setOpen(true)
    }

    const handleClose = () =>{
        setOpen(false)
    }

    //destructuramos el context Modal
    const {informacion, guardarReceta, guardarIdReceta}=useContext(ModalContext);

    //Muestra y formatea los ingredientes

    const mostrarIngredientes = informacion =>{
        let ingredientes = [];
        for (let i = 0; i < 16; i++) {
            
            if( informacion[`strIngredient${i}`]){
                ingredientes.push(
                   <li key={i}>{informacion[`strIngredient${i}`] } {informacion[`strMeasure${i}`]}
                   </li>
                )
            }
            
        }

        return ingredientes;
    }
    return (  
        <div className="col-md-4 mb-3">
            <div className="card">
                <h2 className="card-header">
                    {receta.strDrink}
                </h2>
                <img className="card-img-top" src={receta.strDrinkThumb} alt={receta.strDrink}/>
                <div className="card-body">
                    <button
                        type="button"
                        className="btn btn-block btn-primary"   
                        onClick={()=>{
                            handleOpen();
                            guardarIdReceta(receta.idDrink)
                        }}
                        >
                            Ver Receta
                    </button>

                    <Modal
                    className="modal-material-ui"
                    open={open}
                    onClose={()=>{
                        guardarIdReceta(null)
                        guardarReceta({})
                        handleClose();
                        
                    }
                    }
                    >
                      <div 
                        style={modalStyle}
                        className={classes.paper}
                      >
                        <h2>{informacion.strDrink}</h2>
                        <h3 className="mt-4">Intrucciones</h3>
                        <p>{informacion.strInstructions}</p>
                        <img 
                            className="img-fluid my-4" 
                            src={informacion.strDrinkThumb} 
                            alt={informacion.strDrink}/>
                        <ul>
                           { mostrarIngredientes(informacion)}
                        </ul>
                    </div>  
                    </Modal>
                </div>
            </div>
        </div>
    );
}
 
export default Recetas;